import React from 'react'
import classnames from 'classnames'
import moment from 'moment'
import 'moment/locale/ko'
import styles from '@/styles/modules/_list.module.scss'
import { Link } from 'react-router-dom'
import { RoomList } from '@/types/chat.type'

const ChatListBox: React.FC<RoomList> = ({
  roomId,
  senderName,
  senderProfileImage,
  recentSendMessage,
  recentSendTime,
  noReadCount,
}: RoomList) => {
  const convertTime = (time: string): string => {
    const momentTime = moment(time)

    return momentTime.isSame(new Date(), 'day')
      ? momentTime.format('HH:mm')
      : momentTime.locale('kr').format('dddd')
  }

  return (
    <li>
      <Link to={`/room/${roomId}`} className={styles.chat_listbox}>
        <div className={styles.chat_listbox_profile}>
          <img
            src={`${process.env.PUBLIC_URL}/data/${senderProfileImage}`}
            alt={senderName}
          />
        </div>
        <div className={styles.chat_listbox_text}>
          <h4 className="text-ellipsis">{senderName}</h4>
          <p className="text-ellipsis">{recentSendMessage}</p>
        </div>
        <div className={styles.chat_listbox_info}>
          <span className={styles.time}>{convertTime(recentSendTime)}</span>
          <span className={classnames(styles.badge, { opacity: !noReadCount })}>
            {noReadCount}
          </span>
        </div>
      </Link>
    </li>
  )
}

export default ChatListBox
