import React, { useEffect, useRef, useState } from 'react'
import { useParams } from 'react-router-dom'
import classnames from 'classnames'
import moment from 'moment'
import Header from '@/components/layouts/Header'
import Button from '@/components/commons/ImageButton'
import NotFound from '@/routes/NotFound'
import MessageBox from '@/components/Room/MessageBox'
import DateDivider from '@/components/Room/DateDivider'
import HeaderStyles from '@/styles/modules/_header.module.scss'
import RoomStyles from '@/styles/modules/_room.module.scss'
import Back from '@/assets/images/icons/icon-back.png'
import Photo from '@/assets/images/icons/icon-photo.png'
import Search from '@/assets/images/icons/icon-search.png'
import Send from '@/assets/images/icons/icon-send.png'
import { getRoomMessages, getMyUserNo, putMessage } from '@/api'
import { MessageInfo, Message } from '@/types/chat.type'

const Room: React.FC = () => {
  const roomId = useParams().room_id
  const [isNotFoundPage, setNotFoundPage] = useState(false)
  const [messagePocket, setMessagePocket] = useState('')
  const [messageInfo, setMessageInfo] = useState<MessageInfo>({} as MessageInfo)
  const [isShowImageBox, setStateImageBox] = useState(false)
  const messageBoxRef = useRef<HTMLDivElement>(null)

  const scrollToBottom = () => {
    if (messageBoxRef.current) {
      const scroll =
        messageBoxRef.current.scrollHeight - messageBoxRef.current.clientHeight
      messageBoxRef.current.scrollTo(0, scroll)
    }
  }

  const getChatDetail = () => {
    getRoomMessages(`${process.env.PUBLIC_URL}/data/room/${roomId}.json`)
      .then((data: MessageInfo) => setMessageInfo(data))
      .catch((e) => setNotFoundPage(true))
  }

  const onKeyPress = () => {
    if ((event as KeyboardEvent).key === 'Enter') sendMessage()
  }

  const sendMessage = (): void => {
    const isImageSend = (event?.target as HTMLImageElement).dataset.src

    if (messagePocket || isImageSend) {
      const sendData: Message = {
        userNo: getMyUserNo(),
        message: messagePocket || ' .',
        attachImage: isImageSend ?? '',
        sendTime: moment().utc().format(),
        read: false,
      }

      setMessageInfo((prevState: MessageInfo) => ({
        ...prevState,
        messageList: [...prevState.messageList, sendData],
      }))

      putMessage(`${process.env.PUBLIC_URL}/data/room/${roomId}.json`, sendData)
      setMessagePocket('')
    }
  }

  useEffect(getChatDetail, [])
  useEffect(scrollToBottom, [messageInfo.messageList?.length])

  return (
    <>
      {isNotFoundPage ? (
        <NotFound />
      ) : (
        <>
          <Header title={messageInfo.senderName}>
            <Button
              image={Back}
              alt=""
              onClick={() => {
                history.back()
              }}
              className={classnames(HeaderStyles.header_icon, 'fl-left')}
            />
            <div className="fl-right">
              <Button
                image={Photo}
                alt=""
                onClick={() => setStateImageBox(!isShowImageBox)}
                className={HeaderStyles.header_icon}
              />
              <Button
                image={Search}
                alt=""
                onClick={() => {
                  console.log('test')
                }}
                className={classnames(
                  HeaderStyles.header_icon,
                  HeaderStyles.header_icon_interval
                )}
              />
            </div>
            {isShowImageBox ? (
              <ul className={RoomStyles.image_storage}>
                {[0, 1, 2, 3, 4, 5, 6].map((n) => {
                  return (
                    <li className={RoomStyles.image_column}>
                      <img
                        src={`${process.env.PUBLIC_URL}/data/photos/photo-${n}.png`}
                        data-src={`${process.env.PUBLIC_URL}/data/photos/photo-${n}.png`}
                        alt=""
                        onClick={sendMessage}
                      />
                    </li>
                  )
                })}
              </ul>
            ) : (
              ''
            )}
          </Header>

          <section className={RoomStyles.room_container}>
            <div className={RoomStyles.message_container} ref={messageBoxRef}>
              {messageInfo.messageList?.map(
                (message: Message, index: number, origin: Message[]) => {
                  const isViewDate =
                    moment(message.sendTime).format('DD') !==
                      moment(origin[index - 1]?.sendTime).format('DD') ||
                    index === 0

                  const isViewTime =
                    message.userNo !== origin[index + 1]?.userNo ||
                    moment(message.sendTime).format('hhmm') !==
                      moment(origin[index + 1]?.sendTime).format('hhmm')

                  return (
                    <>
                      {isViewDate ? (
                        <DateDivider
                          date={moment(message.sendTime).format(
                            'YYYY M D'
                          )}
                        />
                      ) : (
                        ''
                      )}
                      <MessageBox
                        key={message.userNo + index}
                        userNo={message.userNo}
                        message={message.message}
                        sendTime={isViewTime ? message.sendTime : ''}
                        attachImage={message.attachImage}
                        read={message.read}
                      />
                    </>
                  )
                }
              )}
            </div>
            <div className={RoomStyles.message_input}>
              <div className={RoomStyles.message_input_textarea}>
                <input
                  type="text"
                  placeholder=" "
                  value={messagePocket}
                  onChange={(e) => setMessagePocket(e.target.value)}
                  onKeyPress={onKeyPress}
                />
              </div>
              <div className={RoomStyles.message_input_buttonarea}>
                <Button image={Send} alt="" onClick={sendMessage} />
              </div>
            </div>
          </section>
        </>
      )}
    </>
  )
}

export default Room
