import { Message, MessageInfo, RoomList } from '@/types/chat.type'

const S: Storage = sessionStorage

const fetchData = async (endpoint: string) => {
  if (S.getItem(endpoint)) {
    return JSON.parse(S.getItem(endpoint) ?? '')
  } else {
    const response = await fetch(endpoint)
      .then((response: Response) => response.json())
      .then((data) => {
        S.setItem(endpoint, JSON.stringify(data))
        return data
      })
      .catch((e) => console.error(e))

    return response
  }
}

const getRooms = async (endpoint: string) => {
  const rooms: RoomList[] = await fetchData(endpoint)
  return rooms.sort(
    (a: RoomList, b: RoomList) =>
      new Date(b.recentSendTime).getTime() -
      new Date(a.recentSendTime).getTime()
  )
}

const getRoomMessages = async (endpoint: string) => {
  const messages: MessageInfo = await fetchData(endpoint)

  //  
  S.setItem(
    endpoint,
    JSON.stringify({
      ...messages,
      messageList: messages.messageList.map((m: Message) => {
        return {
          ...m,
          read: m.userNo !== getMyUserNo() ? true : m.read,
        }
      }),
    })
  )

  // last message update
  const roomNo: number = parseInt(endpoint.match(/room\/([0-9]*)\.json/)![1])
  fetchData('/data/rooms.json').then((rooms: RoomList[]) => {
    rooms[roomNo].noReadCount = 0

    S.setItem('/data/rooms.json', JSON.stringify(rooms))
  })

  return messages
}

const putMessage = async (endpoint: string, data: Message) => {
  const messages: MessageInfo = JSON.parse(S.getItem(endpoint) || '')
  messages.messageList.push(data)
  S.setItem(endpoint, JSON.stringify(messages))

  // last message update
  const roomNo: number = parseInt(endpoint.match(/room\/([0-9]*)\.json/)![1])
  fetchData('/data/rooms.json').then((rooms: RoomList[]) => {
    rooms[roomNo].recentSendMessage = data.message ?? ''
    rooms[roomNo].recentSendTime = data.sendTime
    rooms[roomNo].noReadCount = 0

    S.setItem('/data/rooms.json', JSON.stringify(rooms))
  })
}

const getMyUserNo = () => 202918

export { fetchData, putMessage, getMyUserNo, getRoomMessages, getRooms }
