import React from 'react'
import classnames from 'classnames'
import moment from 'moment'
import RoomStyles from '@/styles/modules/_room.module.scss'
import { Message } from '@/types/chat.type'
import { getMyUserNo } from '@/api'

const MessageBox: React.FC<Message> = ({
  userNo,
  message,
  sendTime,
  attachImage,
}: Message) => {
  const messageClass = getMyUserNo() === userNo ? RoomStyles.me : RoomStyles.you

  const getMessageType = () => {
    return attachImage ? (
      <div className={RoomStyles.image_box}>
        <div className={RoomStyles.box_square}>
          <img src={attachImage} alt="" />
        </div>
      </div>
    ) : (
      <div className={classnames(RoomStyles.message_column, RoomStyles.text)}>
        <div className={classnames(RoomStyles.message_box, messageClass)}>
          {message}
        </div>
      </div>
    )
  }

  return (
    <div className={classnames(RoomStyles.message_row, messageClass)}>
      {getMessageType()}

      {sendTime ? (
        <div className={classnames(RoomStyles.message_column, RoomStyles.time)}>
          {moment(sendTime).format('HH:mm')}
        </div>
      ) : (
        ''
      )}
    </div>
  )
}

export default MessageBox
