import React, { useEffect, useState } from 'react'
import classnames from 'classnames'
import Header from '@/components/layouts/Header'
import Button from '@/components/commons/ImageButton'
import ChatListBox from '@/components/ChatList/ChatListBox'
import HeaderStyles from '@/styles/modules/_header.module.scss'
import ListStyles from '@/styles/modules/_list.module.scss'
import MenuBar from '@/assets/images/icons/icon-menubar.png'
import Member from '@/assets/images/icons/icon-member.png'
import { RoomList } from '@/types/chat.type'
import { getRooms } from '@/api'

const ChatList: React.FC = () => {
  const [roomList, setRoomList] = useState<RoomList[]>([])

  const getRoomList = () => {
    getRooms(`${process.env.PUBLIC_URL}/data/rooms.json`).then(
      (data: RoomList[]) => setRoomList(data)
    )
  }

  useEffect(getRoomList, [])
  return (
    <>
      <Header title="">
        <Button
          image={MenuBar}
          alt=""
          onClick={() => {
            console.log('test')
          }}
          className={classnames(HeaderStyles.header_icon, 'fl-left')}
        />
        <Button
          image={Member}
          alt=""
          onClick={() => {
            console.log('test')
          }}
          className={classnames(HeaderStyles.header_icon, 'fl-right')}
        />
      </Header>

      <ul className={ListStyles.chat_list_container}>
        {roomList.map((room) => (
          <ChatListBox
            key={room.roomId}
            roomId={room.roomId}
            senderName={room.senderName}
            senderNo={room.senderNo}
            senderProfileImage={room.senderProfileImage}
            recentSendMessage={room.recentSendMessage}
            recentSendTime={room.recentSendTime}
            noReadCount={room.noReadCount}
          />
        ))}
      </ul>
    </>
  )
}

export default ChatList
